import React, { Fragment, useEffect, useState, useContext } from "react";

import List from '@mui/material/List';
import Box from '@mui/material/Box';
import ListItem from '@mui/material/ListItem';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemText from '@mui/material/ListItemText';
import ListItemAvatar from '@mui/material/ListItemAvatar';
import Checkbox from '@mui/material/Checkbox';
import SpeedDial from '@mui/material/SpeedDial';
import SpeedDialIcon from '@mui/material/SpeedDialIcon';
import SpeedDialAction from '@mui/material/SpeedDialAction';
import { useNavigate } from "react-router-dom";

import Avatar from '@mui/material/Avatar';
import { deepOrange, deepPurple } from '@mui/material/colors';
import { fetchClients, saveClient } from '../../data/db'

import { AppContext } from "../../App";
import { ConstructionOutlined } from "@mui/icons-material";

export default function Clients(props) {




    const appState = useContext(AppContext);

    const navigate = useNavigate();
    const [checked, setChecked] = React.useState([]);

    const handleToggle = (value) => () => {
        const currentIndex = checked.indexOf(value);
        const newChecked = [...checked];

        if (currentIndex === -1) {
            newChecked.push(value);
        } else {
            newChecked.splice(currentIndex, 1);
        }

        setChecked(newChecked);
    };




    return (
        <div>
            {appState.clients != null &&
                <List dense sx={{ padding: "0", minWidth: '100%', maxWidth: 360, bgcolor: 'background.paper' }}>
                    {appState.clients.map((user, index) => {
                        const labelId = `checkbox-list-secondary-label-${index}`;
                        return (<Fragment key={index}>

                            <ListItem sx={{ paddingBlock: "5px" }}
                                key={user.id}
                                secondaryAction={
                                    <Checkbox
                                        edge="end"
                                        onChange={handleToggle(user.id)}
                                        checked={checked.indexOf(user.id) !== -1}
                                        inputProps={{ 'aria-labelledby': user.id }}
                                    />
                                }
                                onClick={() => {
                                    navigate(`/client/${user.id}`);
                                }}
                                disablePadding
                            >
                                <ListItemButton >
                                    <ListItemAvatar>
                                        <Avatar sx={{ bgcolor: deepOrange[500] }}>
                                            {`${user.lastName.toString().substring(0, 1).toUpperCase()}${user.firstName.toString().substring(0, 1).toUpperCase()}`}
                                        </Avatar>
                                    </ListItemAvatar>
                                    <ListItemText id={user.id} primary={`${user.lastName} ${user.firstName}`} />
                                </ListItemButton>
                            </ListItem>
                            <div style={{ minHeight: "5px", backgroundColor: 'gray' }}></div>
                        </Fragment>
                        );
                    })}
                </List>
            }
            <SpeedDial
                ariaLabel="SpeedDial basic example"
                sx={{ position: 'absolute', bottom: 16, right: 16 }}
                icon={<SpeedDialIcon />}
                onClick={() => {
                    saveClient({ firstName: "test 2", lastName: "test 1" })
                }}
            >

            </SpeedDial>

        </div>)

}