import React, { useEffect, useState } from "react";
import { useParams, useNavigate } from "react-router-dom";
import { Box, Paper } from '@mui/material';
import TextField from '@mui/material/TextField';

import CssBaseline from '@mui/material/CssBaseline';
import Toolbar from '@mui/material/Toolbar';

import Typography from '@mui/material/Typography';
import IconButton from '@mui/material/IconButton';
import AppBar from '@mui/material/AppBar';


import BackIcon from '@mui/icons-material/KeyboardBackspace';
import SaveIcon from '@mui/icons-material/Save';
import DeleteIcon from '@mui/icons-material/DeleteForever';

import { fetchDoc, saveDoc , delDoc} from '../../data/db'

import { SavedSearchOutlined } from "@mui/icons-material";



export default function Client(props) {
    const { id } = useParams();
    const navigate = useNavigate();
    console.log(id);

    const [doc, setDoc] = useState(null)

    useEffect(() => {
        fetchDoc(id, 'clients').then(setDoc)
    }, [id])


    const handleChange = (event) => {


        setDoc((prevDoc) => {
            var tmp = { ...prevDoc }

            tmp[event.target.id] = event.target.value;
            return (tmp);

        })

    }

    return (
        <Box sx={{ display: 'flex' }}>
            <CssBaseline />
            <AppBar position="fixed">
                <Toolbar style={{ display: "flex", flexDirection: "row", justifyContent: "space-between" }}>



                    <IconButton
                        color="inherit"

                        onClick={() => { navigate(-1) }}
                        edge="start"
                        style={{ paddingInlineEnd: 20 }}

                    >
                        <BackIcon />
                    </IconButton>
                    <Typography variant="h6" noWrap component="div" style={{ flex: 50 }} >
                        {doc ? doc.lastName : ''}
                    </Typography>


                    <IconButton
                        color="inherit"
                        onClick={() => {
                            delDoc(id, 'clients').then(() => navigate(-1))

                        }}
                        style={{ paddingInlineEnd: 20 }}

                    >
                        <DeleteIcon />
                    </IconButton>

                    <IconButton
                        color="inherit"
                        onClick={() => {
                            saveDoc(id, 'clients', doc)
                        }}
                        style={{ paddingInlineEnd: 20 }}

                    >
                        <SaveIcon />
                    </IconButton>

                </Toolbar>
            </AppBar>

            <Box style={{ padding: "10px", marginTop: "65px", marginInline: "10px", minWidth: "100%", display: 'flex', flexDirection: "column" }}>

                <TextField
                    id="firstName"
                    label="Όνομα"
                    variant="standard"
                    value={doc ? doc.firstName : ''}
                    style={{ marginBottom: "10px" }}
                    onChange={handleChange}
                />
                <TextField
                    id="lastName"
                    label="Επύθετο"
                    variant="standard"
                    value={doc ? doc.lastName : ''}
                    style={{ marginBottom: "10px" }}
                    onChange={handleChange} />

            </Box>

            <TextField id="standard-basic" label="Όνομα" variant="standard" value={doc ? doc.firstName : ''} />
            <TextField id="standard-basic" label="Επύθετο" variant="standard" value={doc ? doc.lastName : ''} />
        </Box>
    )

}