import "./App.css";
import React, { useEffect, useState, createContext, useReducer } from "react";
import { Route, Routes, HashRouter } from "react-router-dom";
import Login from "./forms/loginForms/Login";
import Register from "./forms/loginForms/Register";
import Reset from "./forms/loginForms/Reset";
import Dashboard from "./forms/Dashboard";
import Client from "./forms/subForms/Client";
import { AppState } from "./data/AppState"

import { auth, db, logout } from "./data/firebase";
import { query, collection, getDocs, where, addDoc, getDoc, doc, setDoc, deleteDoc, onSnapshot } from "firebase/firestore";

let deferredPrompt;


export const AppContext = createContext(null);

function App() {

  const [installable, setInstallable] = useState(false);
  const [appState, setAppState] = useState({});

  useEffect(() => {

    if (!appState.clients) {
      const q = query(collection(db, "clients"));
      const unsubscribe = onSnapshot(q, (querySnapshot) => {
        setAppState((prevState) => { return { ...prevState, clients: querySnapshot.docs.map(d => { return { ...d.data(), id: d.id } }) } });
      });
    }


  }, [appState])

  useEffect(() => {
    window.addEventListener("beforeinstallprompt", (e) => {
      // Prevent the mini-infobar from appearing on mobile
      e.preventDefault();
      // Stash the event so it can be triggered later.
      deferredPrompt = e;
      // Update UI notify the user they can install the PWA
      setInstallable(true);
    });

    window.addEventListener('appinstalled', () => {
      // Log install to analytics
      console.log('INSTALL: Success');
    });
  }, []);

  const handleInstallClick = (e) => {
    // Hide the app provided install promotion
    setInstallable(false);
    // Show the install prompt
    deferredPrompt.prompt();
    // Wait for the user to respond to the prompt
    deferredPrompt.userChoice.then((choiceResult) => {
      if (choiceResult.outcome === 'accepted') {
        console.log('User accepted the install prompt');
      } else {
        console.log('User dismissed the install prompt');
      }
    });
  };

  return (
    <AppContext.Provider value={appState}>
      <div className="app" style={{ position: "relative" }}>
        {installable &&
          <div style={{ position: "absolute", top: 10, right: 10, }}>
            <button style={{ padding: "6px", backgroundColor: "#ddf" }} onClick={handleInstallClick}>
              Εγκατάσταση Εφαρμογής
            </button>
          </div>
        }
        <HashRouter>
          <Routes>
            <Route exact path="/" element={<Dashboard />} />
            <Route exact path="/login" element={<Login />} />
            <Route exact path="/client/:id" element={<Client />} />
            <Route exact path="/client" element={<Client />} />
            <Route exact path="/register" element={<Register />} />
            <Route exact path="/reset" element={<Reset />} />
            <Route exact path="/dashboard/:menuId" element={<Dashboard />} />
          </Routes>
        </HashRouter>
      </div>
    </AppContext.Provider>
  );
}

export default App;
