import React, { useEffect, useState, Fragment } from "react";
import { useParams } from "react-router-dom";

import { styled, useTheme } from '@mui/material/styles';
import Box from '@mui/material/Box';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import Button from '@mui/material/Button';
import CssBaseline from '@mui/material/CssBaseline';
import Toolbar from '@mui/material/Toolbar';

import Typography from '@mui/material/Typography';
import IconButton from '@mui/material/IconButton';
import MoreVertIcon from '@mui/icons-material/MoreVert';
import BottomNavigation from '@mui/material/BottomNavigation';
import BottomNavigationAction from '@mui/material/BottomNavigationAction';
import HomeIcon from '@mui/icons-material/Home';
import GroupIcon from '@mui/icons-material/Group';
import ArchiveIcon from '@mui/icons-material/Archive';
import Paper from '@mui/material/Paper';
import AppBar from '@mui/material/AppBar';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';

import { useAuthState } from "react-firebase-hooks/auth";
import { auth, db, logout } from "../data/firebase";
import { useNavigate } from "react-router-dom";



import SubFormMain from './subForms/Main';
import SubFormClients from './subForms/Clients';

import { fetchUserName } from '../data/db'
import { integerPropType } from "@mui/utils";

const drawerWidth = 240;


export default function Dashboard() {

    const [access, setAccess] = React.useState(null);
    const [user, loading, error] = useAuthState(auth);
    const [name, setName] = useState(null);

    const { menuId } = useParams();
    const navigate = useNavigate();

    //menu start
    const [anchorEl, setAnchorEl] = React.useState(null);
    const open = Boolean(anchorEl);
    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
    };
    const handleClose = (e) => {

        if (e == "Αποσύνδεση") logout()



        setAnchorEl(null);

    };
    const options = [
        'Αποσύνδεση'
    ];
    //menu ends


    useEffect(() => {
    }, [menuId])

    useEffect(async () => {
        if (loading) return;
        if (!user) return navigate("/login");

        const un = await fetchUserName(user);
        if (un == -1) setAccess(false); else setName(un);

        //fetchClients()
    }, [user, loading]);

    const theme = useTheme();


    return (
        <>

            {access === false &&

                <Box style={{
                    height: "100vh",
                    width: "100vw",
                    display: "flex",
                    alignItems: "center",
                    justifyContent: "center",
                    flexDirection: "column"
                }}>


                    <Card sx={{ maxWidth: 345 }}>

                        <CardContent>
                            <Typography gutterBottom variant="h5" component="div">
                                Πρόβλημα
                            </Typography>
                            <Typography variant="body2" color="text.secondary">
                                Δεν έχετε δικαίωμα πρόσβασης στα δεδομένα της εφαρμογής
                            </Typography>
                        </CardContent>
                        <CardActions style={{ justifyContent: "end", }}>

                            <Button size="small" onClick={logout} >Αποσyνδεση</Button>
                        </CardActions>
                    </Card>



                </Box>

            }
            {name !== null &&

                <Box sx={{ display: 'flex' }}>
                    <CssBaseline />
                    <AppBar position="fixed">
                        <Toolbar style={{ display: "flex", flexDirection: "row", justifyContent: "space-between" }}>

                            <Typography variant="h6" noWrap component="div">
                                Elite Training - {name ? name : ''}
                            </Typography>
                            <IconButton
                                color="inherit"
                                aria-label="open drawer"
                                onClick={handleClick}
                                edge="start"
                                style={{ paddingInlineEnd: 0 }}

                            >
                                <MoreVertIcon />
                            </IconButton>
                            <Menu
                                id="long-menu"
                                MenuListProps={{
                                    'aria-labelledby': 'long-button',
                                }}
                                anchorEl={anchorEl}
                                open={open}
                                onClose={handleClose}
                            > {options.map((option) => (
                                <MenuItem key={option} onClick={(event) => handleClose(option, event)}>
                                    {option}
                                </MenuItem>
                            ))}</Menu>
                        </Toolbar>
                    </AppBar>
                    <Box sx={{ padding: "10px", position: 'fixed', top: "55px", left: 0, right: 0, bottom: "56px", backgroundColor: "gray" }} elevation={3}>
                        {parseInt(menuId ? menuId :0 ) === 0 && <SubFormMain />}
                        {parseInt(menuId ? menuId :0) === 1 && <SubFormClients />}
                    </Box>

                    <Paper sx={{ position: 'fixed', bottom: 0, left: 0, right: 0 }} elevation={3}>
                        <BottomNavigation
                            
                            value={parseInt(menuId ? menuId :0)}
                            onChange={(event, newValue) => {
                                navigate(`/dashboard/${newValue}`)

                            }}
                        >
                            <BottomNavigationAction icon={<HomeIcon />} />
                            <BottomNavigationAction icon={<GroupIcon />} />
                            <BottomNavigationAction icon={<ArchiveIcon />} />
                            <BottomNavigationAction icon={<ArchiveIcon />} />
                            <BottomNavigationAction icon={<ArchiveIcon />} />

                        </BottomNavigation>
                    </Paper>
                </Box>
            }
        </>
    );
}

