import { auth, db, logout } from "../data/firebase";
import { query, collection, getDocs, where, addDoc, getDoc, doc, setDoc, deleteDoc, onSnapshot } from "firebase/firestore";
import { useState } from "react";




let clients = null;
export const initApp = async ()=>{

await loadClients()
}



const loadClients = async () => {
  const q = query(collection(db, "clients"));
  const unsubscribe = onSnapshot(q, (querySnapshot) => {
    clients= querySnapshot.docs.map(d => { return { ...d.data(), id: d.id } });
  });
}

export const saveClient = async (client) => {
  const docRef = await addDoc(collection(db, "clients"), client);
}

export const fetchClients = async () => {
  try {
    const q = query(collection(db, "clients"));
    const snapshot = await getDocs(q);
    const data = snapshot.docs.map(d => { return { ...d.data(), id: d.id } });

    return data;
  } catch (err) {
    console.error(err);
    //   alert("An error occured while fetching clients data:" + err);
    return [];
  }
};

export const fetchUserName = async (user) => {
  try {
    const q = query(collection(db, "users"), where("uid", "==", user?.uid));
    const doc = await getDocs(q);
    const data = doc.docs[0].data();

    return data.name;
  } catch (err) {
    console.error(err);
    //alert("An error occured while fetching user data");
    return -1;
  }
};

export const fetchDoc = async (uid, dbCollection) => {
  const docRef = doc(db, dbCollection, uid);
  const docSnap = await getDoc(docRef);

  if (docSnap.exists()) {

    return docSnap.data();
  } else {
    // doc.data() will be undefined in this case
    console.log("No such document!");
    return {};
  }
};

export const saveDoc = async (uid, dbCollection, document) => {
  await setDoc(doc(db, dbCollection, uid), document);
}

export const delDoc = async (uid, dbCollection, document) => {
  return deleteDoc(doc(db, dbCollection, uid));
}




export const allDocs = async (dbCollection) => {
  switch (dbCollection) {
    case 'clients':
      if (clients == null) loadClients()
      return clients ? clients : []
      break;

    default:
      return []
      break;
  }

}

